<template>
  <div class="container-app">
    <div class="container-house-gradient" v-bind:class="HouseGradient">
    
    </div>
    <div class="container-quiz">
      <div class="quiz-main">
        <div class="quiz-header" v-bind:class="HouseColor">
          <div class="box-question" v-show="!ShowingResults">
            <h1>{{GetCurrentQuestion()}}</h1>
          </div>
          <img class="house-icon" v-show="ShowingResults" :src="HouseIcon"/>
        </div>
        <div class="box-answers" ref="answers" v-bind:class="HouseColor">
          <button class="answer-button" v-show="!ShowingResults" v-on:click="PickAnswer(0)">{{GetAnswer(0)}}</button>
          <button class="answer-button" v-show="!ShowingResults" v-on:click="PickAnswer(1)">{{GetAnswer(1)}}</button>
          <button class="answer-button" v-show="!ShowingResults" v-on:click="PickAnswer(2)">{{GetAnswer(2)}}</button>
          <button class="answer-button" v-show="!ShowingResults" v-on:click="PickAnswer(3)">{{GetAnswer(3)}}</button>
          <div class="box-results" v-show="ShowingResults" :class="ShowingResults ? 'white-text' : ''">
            <h1>{{"You were placed in the House of " + HouseResult}}</h1>
            <p>{{HouseDescription}}</p>
          </div>
        </div>
        <div class="quiz-footer" v-bind:class="HouseColor">
          <img id="retry-button" src="./assets/retry.png" v-show="ShowingResults" v-on:click="Restart()"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import Questions from './assets/QuestionDatabase.json';

export default {
  name: 'App',
  mounted: function() {
    this.PopulateQuestions();
  },
  data() {
    return {
      NumHouseQuestions: Questions.HouseQuestions,
      NumTotalQuestions: Questions.TotalQuestions,
      QuestionDatabase: Questions.questions,
      ActiveQuestions: [],
      CurrentQuestion: 0,
      UserData: {
        'explorer': 0,
        'conqueror': 0,
        'visionary': 0
      },
      ShowingResults: false,
      HouseResult: '',
      HouseDescription: '',
      HouseColor: {
        'color-explorer': false,
        'color-conqueror': false,
        'color-visionary': false,
        'color-travis': false
      },
      HouseGradient: {
        'gradient-explorer': false,
        'gradient-conqueror': false,
        'gradient-visionary': false,
        'gradient-travis': false
      },
      HouseIcon: ''
    }
  },
  methods: {
    PopulateQuestions: function() {
      var vm = this;

      vm.ActiveQuestions = [];

      vm.QuestionDatabase.forEach(element => {
        var array = element.answers;
        for(var i = array.length - 1; i > 0; i--) {
          var j = Math.floor(Math.random() * (i + 1));
          var temp = array[i];
          array[i] = array[j];
          array[j] = temp;
        }
      });

      for(var i = 0; i < vm.NumTotalQuestions * vm.NumHouseQuestions; i += vm.NumHouseQuestions) {
        vm.ActiveQuestions.push(vm.QuestionDatabase[Math.floor(Math.random() * ((i + (vm.NumHouseQuestions - 1)) - i + 1) + i)]);
      }
    },
    PickAnswer: function(selectedAnswer) {
      var vm = this;
      var answer = vm.ActiveQuestions[vm.CurrentQuestion].answers[selectedAnswer];

      vm.UserData[answer.house] += answer.value;

      vm.CurrentQuestion++;

      if(vm.CurrentQuestion >= vm.NumTotalQuestions) {
        vm.ShowResults();
      }

      //console.log(vm.UserData['explorer'] + ' ' + vm.UserData['conqueror'] + ' ' + vm.UserData['visionary'] + ' ');
    },
    GetCurrentQuestion: function() {
      var vm = this;

      if(vm.ActiveQuestions[vm.CurrentQuestion] != null) {
        return vm.ActiveQuestions[vm.CurrentQuestion].question;
      } else {
        return '';
      }
    },
    GetAnswer: function(answerID) {
      var vm = this;
      var question = vm.ActiveQuestions[vm.CurrentQuestion];

      if(question != null) {
        return question.answers[answerID].answer;
      } else {
        return '';
      }
    },
    ShowResults: function() {
      var vm = this;

      vm.ShowingResults = true;

      vm.UpdateHouseResults();
    },
    UpdateHouseResults: function() {
      var vm = this;

      var house = 'explorer';
      if(vm.UserData['conqueror'] > vm.UserData[house])
        house = 'conqueror';
      if(vm.UserData['visionary'] > vm.UserData[house])
        house = 'visionary';

      if(vm.UserData['conqueror'] == 4.5) {
        house = 'travis';
      }

      vm.HouseColor[`color-${house}`] = true;
      vm.HouseGradient[`gradient-${house}`] = true;
      vm.HouseIcon = `assets/${house}-logo.png`;
      switch(house) {
        case 'explorer':
          vm.HouseResult = 'Explorers';
          vm.HouseDescription = "Thoughtful and inquisitive, an Explorer is well balanced.";
          break;
        case 'conqueror':
          vm.HouseResult = 'Conquerors';
          vm.HouseDescription = "Brave and decisive, a Conqueror knows themself well.";
          break;
        case 'visionary':
          vm.HouseResult = 'Visionaries'; 
          vm.HouseDescription = "Wise and knowledgable, a Visionary sees what most cannot.";
          break;
        case 'travis':
          vm.HouseResult = "Travis Scott"
          vm.HouseDescription = "Talented, professional, well fed from the Travis Scott meal, consider yourself a legend amongst all.";
          break;
        default:
          vm.HouseResult = 'funny gifs?';
          vm.HouseDescription = "An error has occured! Please alert the nearest McDonalds employee, tell them Cactus Jack sent you.";
          break;
      }
      vm.HouseResult += '!';
    },
    Restart: function() {
      var vm = this;

      vm.PopulateQuestions();

      vm.CurrentQuestion = 0;
      vm.UserData = {
        'explorer': 0,
        'conqueror': 0,
        'visionary': 0
      };
      vm.ShowingResults = false;
      vm.HouseColor = {
        'color-explorer': false,
        'color-conqueror': false,
        'color-visionary': false,
        'color-travis': false
      };
      vm.HouseGradient = {
        'gradient-explorer': false,
        'gradient-conqueror': false,
        'gradient-visionary': false,
        'gradient-travis': false
      };
    }
  }

}
</script>

